// IMPORTS
// Material UI
import Slide from '@material-ui/core/Slide'
import withStyles from '@material-ui/core/styles/withStyles'

// Externals imports (Dependencies)
import PropTypes from 'prop-types'
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

// Internal imports
import Lista from './List'
import styles from '../../../../resources/theme/stores'
import EnhancedComponent from '../../../components/EnhancedComponent'
import { fetchDistributionCentersPerStores, saveDistributionCentersPerStores } from '../../../distributionCenters/DistributionCentersActions'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class DistributionCentersStores extends EnhancedComponent {
  state = {
    dirty: false,
    fields: {},
  }

  componentDidMount() {
    this.props.fetchDistributionCentersPerStores(this.props.match.params.id)
  }

  onSubmit = values => {
    this.props
      .saveDistributionCentersPerStores(values, this.props.match.params.id)
      .then(data => {
        if (data && data.code === 200) {
          this.props.history.push('/stores')
        }
      })
  }

  onClose = () => {
    this.props.history.push('/stores')
  }

  render() {
    const { classes, items } = this.props
    return (
      <Lista
        data={items}
        classes={classes}
        onClose={this.onClose}
        onSubmit={this.onSubmit}
      />
    )
  }
}

DistributionCentersStores.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
  items: state.distributioncenter.items_store,
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchDistributionCentersPerStores,
      saveDistributionCentersPerStores,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(DistributionCentersStores))
